<template lang="">
  <div id="todo">
    <div class="welcome_t" v-if="welcome">
      <div class="d-flex justify-content-evenly">
        <h3>TodoList App</h3>
        <img src="../assets/img/todolist/todolist-app.png" alt="todolist app">
      </div>
      <hr />
      <div class="d-flex flex-column align-bottom">
        <div class="welcome-input">
          <b-form-input
            id="input-live"
            v-model="name"
            placeholder="Please enter your name"
            class="mt-4"
            :state="nameState"
            aria-describedby="input-live-help input-live-feedback"
            trim
            @keyup.enter="enterPage"
          ></b-form-input>
          <!-- This will only be shown if the preceding input has an invalid state -->
          <b-form-invalid-feedback id="input-live-feedback">
            Enter at least 3 letters
          </b-form-invalid-feedback>
        </div>
        <div class="d-flex justify-content-end mt-2">
          <b-button variant="success" @click="enterPage">OK</b-button>
        </div>
      </div>
    </div>

    <div v-if="!welcome" class="container">
      <header>
        <h1>{{ name }}'s TodoList</h1>
      </header>

      <div class="filters">
        <span
          :class="{ selected: filter === 'all' }"
          @click.prevent="filter = 'all'"
          ><a href="">All ({{ all }})</a></span
        >
        <span
          :class="{ selected: filter === 'todo' }"
          @click.prevent="filter = 'todo'"
          ><a href="">To complete ({{ remaining }})</a></span
        >
        <span
          :class="{ selected: filter === 'done' }"
          @click.prevent="filter = 'done'"
          ><a href="">Completed ({{ completed }})</a></span
        >
      </div>

      <form @submit.prevent=""> <!--To prevent the submit of form-->
        <input
          type="text"
          id="item"
          placeholder="..."
          minlength="2"
          autocomplete="off"
          v-model="newTask"
          @keyup.enter="addTask"
        />
      </form>

      <ul id="list" v-if="todos.length > 0">
        <li
          class="todo"
          :class="{ checked: todo.checked }"
          v-for="todo in filteredTodos"
          :key="todo"
          @click="taskCompleted(todo)"
        >
          {{ todo.name }}
          <span class="trash"
            ><i v-if="todo.checked" class="fa-solid fa-trash"></i
          ></span>
          <span class="marked"
            ><i v-if="!todo.checked" class="fa-solid fa-check"></i
          ></span>
        </li>
      </ul>

      <footer></footer>
    </div>
  </div>
</template>
<script>
export default {
  name: "TodolistView",
  components: {},
  data() {
    return {
      welcome: true,
      name: "",
      todos: [],
      newTask: "",
      filter: "all",
    };
  },
  mounted() { },
  methods: {
    enterPage() {
      if (this.name.length > 2) this.welcome = !this.welcome;
    },
    addTask() {
      if (this.newTask != "")
        this.todos.push({
          name: this.newTask,
          checked: false,
        });
      this.newTask = "";
    },
    taskCompleted(item) {
      if (item.checked == true) {
        var index = this.todos.indexOf(item);
        this.todos.splice(index, 1);
      } else {
        item.checked = !item.checked;
      }
    },
  },
  computed: {
    nameState() {
      return this.name.length > 2 ? true : false;
    },
    all() {
      return this.todos.length;
    },
    remaining() {
      return this.todos.filter((todo) => !todo.checked).length;
    },
    completed() {
      return this.todos.filter((todo) => todo.checked).length;
    },
    filteredTodos() {
      if (this.filter === "todo") {
        return this.todos.filter((todo) => !todo.checked);
      } else if (this.filter === "done") {
        return this.todos.filter((todo) => todo.checked);
      } else {
        return this.todos;
      }
    },
  },
};
</script>
<style lang="css" scoped>
#todo {
  min-height: 100vh;
  padding: 20px;
  background-image: url(../assets/img/todolist/todolist.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  font-family: "Gemunu Libre", sans-serif;
}

.welcome_t {
  width: 300px;
  height: 235px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.597);
  box-shadow: inset 0 2px 2px rgba(0, 0, 0, 0.25);
  padding: 15px;
  border-radius: 9px;
}

.welcome_t h3 {
  text-shadow: 2px 2px 0 rgba(18, 17, 17, 0.207);
  color: #131313;
  letter-spacing: 0.15em;
}

.welcome_t img {
  height: 40px;
}

.welcome_t-input {
  width: 100%;
  min-height: 90px;
}

.container {
  width: 90%;
  padding: 80px 20px;
  background: transparent;
}

/* * HEADER * */
header {
  width: 80%;
  margin: 0 auto;
  text-align: left;
  padding: 20px 0px;
  font-size: 2rem;
  color: #fa6c40;
}

h1 {
  text-transform: capitalize;
  font-size: 1.5em;
  text-shadow: 2px 2px 0 rgba(18, 17, 17, 0.207);
  color: #131313;
  letter-spacing: 0.15em;
}

.filters {
  display: flex;
  justify-content: center;
  width: 80%;
  text-align: center;
  margin: 5px auto;
  border: 1px solid black;
  border-radius: 9px 9px 0px 0px;
  background: #00000078;
}

.filters span {
  width: calc(100% / 3);
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  text-transform: uppercase;
  font-size: 0.8rem;
}

.filters span:nth-child(1) {
  border-radius: 9px 0px 0px 0px;
}

.filters span:nth-child(2) {
  border-radius: 0px 0px 0px 0px;
}

.filters span:nth-child(3) {
  border-radius: 0px 9px 0px 0px;
}

.filters span.selected {
  background: #000000;
}

.filters span a {
  color: rgb(161, 145, 145);
  font-style: unset;
  text-decoration: none;
}

.filters span.selected a {
  color: #88f528;
}

/* * FORM SECTION * */
form {
  text-align: center;
}

form input {
  width: 80%;
  padding: 7px 20px;
  margin-bottom: 20px;
  outline: none;
  border: 1px solid #030303;
  border-radius: 5px;
  background: rgba(204, 200, 200, 0.507);
  transition: all 0.3s ease-in;
  font-size: larger;
}

form input::placeholder {
  color: #000000;
}

form input:focus {
  background: rgb(255, 255, 255);
}

ul {
  width: 80%;
  height: 100%;
  margin: 10px auto;
  padding: 2%;
  text-align: left;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.597);
}

ul li {
  width: 95%;
  margin: 10px auto;
  position: relative;
  list-style: none;
  text-transform: capitalize;
  font-size: 1.2rem;
  padding: 0.7rem;
  cursor: pointer;
  transition: all 0.4s ease;
  word-wrap: break-word;
  color: #000000;
  border: 1px solid #000000;
  border-radius: 5px;
  transition: all 0.5s ease-in;
  line-height: 30px;
}

ul li:nth-child(odd) {
  background: #fcab3e;
  width: 100%;
}

ul li:nth-child(even) {
  background: #739ff9;
  width: 95%;
}

ul li:hover {
  transform: scale(1.1);
}

/*
ul li:before {
  content: "\25C9";
  padding: 0.6rem;
  font-size: 1.3rem;
  color: rgb(9, 255, 0);
}
*/
ul li:after {
  opacity: 0;
}

/*
ul li:hover:after {
  content: "mark as complete";
  opacity: 0;
  font-size: 1.1rem;
  color: #ffffff;
  font-style: italic;
  animation: pseudoClassAnim 1s forwards;
}
*/

ul li.checked {
  background: rgba(128, 128, 128, 0.623);
}

/*
ul li.checked:before {
  content: "\25C9";
  padding: 0.6rem;
  font-size: 1.3rem;
  color: red;
}

ul li.checked:hover:after {
  content: "click to remove";
  font-size: 1.1rem;
  color: #fd4a1d;
  opacity: 0;
}
*/
ul li span {
  position: absolute;
  right: 10px;
}

ul li span.trash {
  color: red;
}

ul li span.marked {
  color: rgb(9, 255, 0);
}

ul li span i {
  visibility: hidden;
}

ul li:hover span i {
  visibility: visible;
}

/* * FOOTER * */
footer {
  position: fixed;
  z-index: -1;
  left: 0;
  bottom: 0;
  height: 100vh;
  width: 100%;
  background-color: #fa6c40;
  color: white;
  text-align: center;
  padding: 3px;
}

/* * KEYFRAMES * */
@keyframes pseudoClassAnim {
  to {
    opacity: 1;
  }
}

/* * BREAKPOINTS * */
@media all and (max-width: 992px) {
  h1 {
    font-size: 1em;
  }
}

@media all and (max-width: 600px) {
  #todo {
    padding: 0;
  }

  .container {
    width: 100%;
    padding: 0 0 50px;
  }

  .filters {
    flex-direction: column;
  }

  .filters span {
    width: 100%;
  }

  .filters span:nth-child(1) {
    border-radius: 9px 9px 0px 0px;
  }

  .filters span:nth-child(3) {
    border-radius: 0px 0px 0px 0px;
  }

  ul li {
    padding: 0px 0px 0px 5px;
    font-size: 0.8rem;
  }

  ul li:hover {
    transform: scale(1);
  }

  ul li:hover:after {
    font-size: 0.7rem;
  }

  ul li:nth-child(even) {
    width: 100%;
  }

  ul li.checked:hover:after {
    margin-top: 2px;
    font-size: 0.7rem;
  }
}
</style>
